import React from 'react'
const data=require('./data/data1')

const Content=(props) => {
    let data=props.data
    return (
        
        <div className="content-wrapper" onClick={event =>  window.location.href=data.url} style={{'cursor': 'pointer'}} >
            <span className="til" >{data.title}</span>
            <p className="para">{data.description}</p>
            <span style={{'fontSize':'15px','fontWeight':'500'}}></span>
            {
                data.tags.map((item,i)=>{
                    return (
                    <span className="tag" key={i}>{item}</span>
                    )
                })
            }
        </div>
    )
}

const NewProjects=()=>{
    return (
        <div>
            {data.map(item => (
                <Content data={item} key={item.title} />
            ))}
        </div>
    )
}

export default NewProjects